import { ChangeDetectorRef, Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { AlertController } from '@ionic/angular';
import { AuthService } from 'src/app/services/auth/auth.service';
import { CommonService } from 'src/app/services/common/common.service';
import { DataService } from 'src/app/services/data/data.service';
import { StateService } from 'src/app/services/state/state.service';

@Component({
  selector: 'app-dashboard',
  templateUrl: './dashboard.page.html',
  styleUrls: ['./dashboard.page.scss'],
})
export class DashboardPage implements OnInit {

  public unreadNotificationsLength: number = 0;

  constructor(
    private dataService: DataService,
    private cd: ChangeDetectorRef,
    private alertCtrl: AlertController,
    private commonService: CommonService,
    private stateService: StateService,
    private authService: AuthService,
    private router: Router,
  ) {
    this.dataService.getUnreadNotifications().subscribe(res => {
      this.unreadNotificationsLength = res.length;
      this.cd.detectChanges();
    });
  }

  ngOnInit() {
  }

  navTo(path) {
    this.commonService.navigateForward(path);
  }

  logout() {
    this.authService.logout().then(resp => {
      localStorage.clear();
      this.router.navigateByUrl('login', { replaceUrl: true });
    }, err => {
      console.error('Error while logout:::::::::\n', err);
      this.commonService.presentToast('unable to login', 'danger');
    });
  }

  async addCourse() {
    const alert = await this.alertCtrl.create({
      header: 'Add Course',
      inputs: [
        {
          name: 'title',
          placeholder: 'Enter course title',
          type: 'text'
        },
        {
          name: 'applicable',
          placeholder: 'Applicable for',
          type: 'text'
        },
        {
          name: 'description',
          placeholder: 'Enter course description',
          type: 'textarea'
        },
      ],
      buttons: [
        {
          text: 'Cancel',
          role: 'cancel'
        }, {
          text: 'Add',
          handler: res => {
            this.dataService.addCourse({ description: res.description, title: res.title, applicable: res.applicable });
          }
        }
      ]
    });
    await alert.present();
  }
}
